<template>
  <div :class="{'signin-container': isPc, 'signin-container-mb': !isPc}">
    <p class="title">登入</p>
    <a-form id="components-form-demo-normal-login" :form="form" class="login-form" @submit="handleSubmit">
      <a-form-item>
        <i class="input-icon" :class="{
        'email-icon-normal': emailState === 1,
        'email-icon-click': emailState === 2,
        'email-icon-error': emailState === 3}"></i>
        <a-input v-decorator="['email',{ rules: [{
                type: 'email',
                message: '郵箱格式錯誤',
              },{ required: true, message: '請輸入電郵' }] }]"
                 placeholder="電郵" @focus="emailState = 2;showError = 0;iconLoading = false" @blur="emailState = 1">
        </a-input>
      </a-form-item>
      <a-form-item>
        <i class="input-icon" :class="{
        'password-icon-normal': passwordState === 1,
        'password-icon-click': passwordState === 2,
        'password-icon-error': passwordState === 3}"></i>
        <a-input v-decorator="['password',{ rules: [{ required: true, message: '請輸入密碼' }] }]"
                 :type="passwordType" placeholder="密碼" @focus="passwordState = 2;showError = 0;iconLoading = false" @blur="passwordState = 1">
        </a-input>
        <i class="input-icon" :class="{'password-show': !passwordShow, 'password-hide': passwordShow}" @click="showPassword"></i>
      </a-form-item>
      <a-form-item>
        <a class="login-form-forgot" @click="goForgetPassword">忘記密碼?</a>
        <a-button type="primary" html-type="submit" :loading="iconLoading" class="login-form-button">登入</a-button>
        <p class="error-mss" :style="{'opacity':showError}">{{errorMss}} </p>
      </a-form-item>
    </a-form>
    <div class="signin-footer">
      <span class="tip">還沒有帳號嗎？</span>
      <a-button class="subscribe-btn" type="primary" ghost @click="goQuickBuy">立即订阅</a-button>
    </div>
  </div>

</template>

<script>
  import PublicHeader from '@/components/PublicHeader.vue';
  export default {
    name: 'SignIn',
    components: {
      PublicHeader
    },
    beforeCreate() {
      this.form = this.$form.createForm(this);
    },
    data() {
      return {
        emailState: 1,
        emailReg: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        passwordState: 1,
        passwordShow: false,
        passwordType: 'password',
        errorMss: '',
        noAccountSignUp: '現在註冊',
        showError: 0,
        iconLoading: false,
        isPc: this.$isPc()
      };
    },
    created() {
      // window.location.href = "whaleblue://test.com?email=dhisuhfiwe@qq.com&token=sdiouhfjwuehfliuahsdliufqwiluehi234hrlkuwh3i4uh";
      //
      // setTimeout(function(){
      //   window.location.href = "whaleblue://test.com?email=dhisuhfiwe@qq.com&token=sdiouhfjwuehfliuahsdliufqwiluehi234hrlkuwh3i4uh";
      // },2000)
    },
    mounted() {
    },
    methods: {
      handleSubmit(e) {
        e.preventDefault();
        // if (this.emailCheck()) {
          this.form.validateFields((err, values) => {
            if (!err) {
              this.iconLoading = true
              this.$http.post(`${process.env.VUE_APP_SERVER_URL}/api/v1/users/login`, values)
                .then((data) => {
                  this.iconLoading = false
                  if (data.status === 201) {
                    localStorage.setItem('email', data.data.email);
                    localStorage.setItem('token', data.data.token);
                    window.zE(function() {
                      if (!!localStorage.getItem("email")) {
                        window.zE.identify({
                          name: localStorage.getItem("email")
                                  .split('@')[0],
                          email: localStorage.getItem("email"),
                        });
                      }
                      window.zE.setLocale('zh-TW');
                    });
                    this.$http.defaults.headers.common.Authorization = `Bearer ${data.data.token}`;
                    // this.$router.push('products');
                    this.$router.push({
                      path: `/my/service`,
                      name: 'service'
                    });
                  } else {
                    this.emailState = 3
                    this.passwordState = 3
                    this.showError = 1
                    // if (data.message.indexOf('Email or Password Invalid') > -1) {
                    this.errorMss = '電郵或密碼錯誤'
                    this.noAccountSignUp = ''
                    // }
                  }
                });
            }
          });
        // }
      },
      showPassword() {
        this.passwordShow = !this.passwordShow
        this.passwordType = this.passwordShow ? 'text': 'password'
      },
      goForgetPassword() {
        this.$router.push('forget-password')
      },
      goQuickBuy() {
        this.$router.push({
          path: '/order/place-order',
          name: 'pay-order'
        })
      },
      // emailCheck() {
      //   this.emailState = 1;
      //   if (this.form.getFieldValue('email') && !this.emailReg.test(this.form.getFieldValue('email'))) {
      //     this.showError = 1;
      //     this.errorMss = '郵箱格式錯誤';
      //     return false
      //   } else {
      //     return true
      //   }
      // },
    },

  };
</script>
